"use client";
import { useState } from "react";
import { uploadService } from "@/services/uploadService";
import { eventService } from "@/services/eventService";
import toast from "react-hot-toast";

export default function UploadEventForm() {
  const [isLoading, setIsLoading] = useState(false);
  const [agreeChecked, setAgreeChecked] = useState(false);
  const [formData, setFormData] = useState({
    picture: null,
    heading: "",
    date: "",
    location: "",
    shortBrief: "",
    description: "",
    video1: "",
    video2: "",
    image1: null,
    image2: null
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleFileChange = (e, fieldName) => {
    const file = e.target.files[0];
    if (file) {
      setFormData(prev => ({ ...prev, [fieldName]: file }));
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    // Validate agreement checkbox
    if (!agreeChecked) {
      toast.error('Please agree to upload the data on website');
      return;
    }

    // Validate required fields
    if (!formData.heading || !formData.date || !formData.location || !formData.shortBrief || !formData.description) {
      toast.error('Please fill all required fields');
      return;
    }

    // Validate event picture
    if (!formData.picture) {
      toast.error('Please upload event picture');
      return;
    }

    setIsLoading(true);
    toast.loading('Creating event...');

    try {
      // Get backend base URL
      const backendBaseURL = process.env.NEXT_PUBLIC_API_URL?.replace('/api', '') || 'http://localhost:5000';

      // Upload event picture
      let pictureUrl = null;
      if (formData.picture) {
        toast.loading('Uploading event picture...');
        try {
          pictureUrl = await uploadService.uploadSingle(formData.picture, 'event');
        } catch (uploadError) {
          console.error('Picture upload error:', uploadError);
          toast.error('Failed to upload event picture');
          setIsLoading(false);
          return;
        }
      }

      // Upload additional images
      const uploadedImages = [];
      
      if (formData.image1) {
        toast.loading('Uploading image 1...');
        try {
          const imageUrl = await uploadService.uploadSingle(formData.image1, 'event');
          uploadedImages.push({ url: `${backendBaseURL}${imageUrl}`, caption: 'Event Image 1' });
        } catch (uploadError) {
          console.error('Image 1 upload error:', uploadError);
          // Continue even if one image fails
        }
      }

      if (formData.image2) {
        toast.loading('Uploading image 2...');
        try {
          const imageUrl = await uploadService.uploadSingle(formData.image2, 'event');
          uploadedImages.push({ url: `${backendBaseURL}${imageUrl}`, caption: 'Event Image 2' });
        } catch (uploadError) {
          console.error('Image 2 upload error:', uploadError);
          // Continue even if one image fails
        }
      }

      // Prepare videos array
      const videos = [];
      if (formData.video1) {
        videos.push({
          url: formData.video1.trim(),
          platform: 'youtube',
          thumbnail: ''
        });
      }
      if (formData.video2) {
        videos.push({
          url: formData.video2.trim(),
          platform: 'youtube',
          thumbnail: ''
        });
      }

      // Prepare event data
      const eventData = {
        heading: formData.heading.trim(),
        shortBrief: formData.shortBrief.trim(),
        description: formData.description.trim(),
        date: formData.date,
        location: formData.location.trim(),
        picture: {
          url: `${backendBaseURL}${pictureUrl}`,
          caption: 'Event Picture'
        },
        videos: videos,
        images: uploadedImages,
        status: 'published'
      };

      toast.loading('Creating event...');

      // Create event
      const response = await eventService.createEvent(eventData);

      toast.dismiss();
      toast.success('Event created successfully! ðŸŽ‰');

      // Reset form
      setFormData({
        picture: null,
        heading: "",
        date: "",
        location: "",
        shortBrief: "",
        description: "",
        video1: "",
        video2: "",
        image1: null,
        image2: null
      });
      setAgreeChecked(false);

      // Reset file inputs
      document.querySelectorAll('input[type="file"]').forEach(input => {
        input.value = '';
      });

    } catch (error) {
      console.error('Event creation error:', error);
      toast.dismiss();
      
      const errorMessage = error?.message || error?.data?.message || 'Failed to create event. Please try again.';
      toast.error(errorMessage);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <>
      <div className="mb-8">
        <h1 className="text-4xl font-bold text-gray-900 mb-2">Upload Events</h1>
        <nav className="text-gray-500 text-sm">
          <ol className="flex space-x-2 items-center">
            <li><a href="/admin" className="hover:underline text-gray-600">Home</a></li>
            <li className="text-gray-400">/</li>
            <li className="text-gray-700">Upload Events</li>
          </ol>
        </nav>
      </div>

      <div className="bg-white rounded-xl shadow-sm border p-8">
        <h2 className="text-xl font-semibold text-blue-900 mb-6">Upload Events On Website</h2>

        <form onSubmit={handleSubmit} className="space-y-6">
          <div>
            <label className="block text-sm font-medium text-red-600 mb-2">
              Upload Event Picture <span className="text-gray-500">(530 x 398 px)</span> <span className="text-red-500">*</span>
            </label>
            <input 
              type="file" 
              accept="image/*"
              onChange={(e) => handleFileChange(e, 'picture')}
              required
              className="border rounded-lg px-3 py-2 w-full text-sm"
            />
            {formData.picture && (
              <p className="text-xs text-gray-500 mt-1">Selected: {formData.picture.name}</p>
            )}
          </div>

          <div>
            <label className="block text-sm font-medium text-green-600 mb-2">
              Event Heading <span className="text-red-500">*</span>
            </label>
            <input 
              type="text" 
              name="heading" 
              value={formData.heading} 
              onChange={handleChange} 
              placeholder="Enter The Heading Of the Event" 
              required
              className="border rounded-lg px-4 py-2 w-full text-sm focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
            />
          </div>

          <div className="grid grid-cols-2 gap-6">
            <div>
              <label className="block text-sm font-medium text-green-600 mb-2">
                Event Date <span className="text-red-500">*</span>
              </label>
              <input 
                type="date" 
                name="date" 
                value={formData.date} 
                onChange={handleChange} 
                required
                className="border rounded-lg px-4 py-2 w-full text-sm focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-green-600 mb-2">
                Event Short Brief <span className="text-red-500">*</span>
              </label>
              <input 
                type="text" 
                name="shortBrief" 
                value={formData.shortBrief} 
                onChange={handleChange} 
                placeholder="Enter Event Short Brief Here" 
                required
                className="border rounded-lg px-4 py-2 w-full text-sm focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
              />
            </div>
          </div>

          <div>
            <label className="block text-sm font-medium text-green-600 mb-2">
              Event Location <span className="text-red-500">*</span>
            </label>
            <input 
              type="text" 
              name="location" 
              value={formData.location} 
              onChange={handleChange} 
              placeholder="Enter Event Location" 
              required
              className="border rounded-lg px-4 py-2 w-full text-sm focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
            />
          </div>

          <div>
            <label className="block text-sm font-medium text-green-600 mb-2">
              Event Description <span className="text-red-500">*</span>
            </label>
            <div className="border rounded-lg p-2">
              <div className="flex items-center gap-2 mb-2 pb-2 border-b">
                <select className="text-sm border rounded px-2 py-1">
                  <option>Normal</option>
                </select>
                <button type="button" className="p-1 hover:bg-gray-100 rounded font-bold">B</button>
                <button type="button" className="p-1 hover:bg-gray-100 rounded italic">I</button>
                <button type="button" className="p-1 hover:bg-gray-100 rounded underline">U</button>
              </div>
              <textarea 
                name="description" 
                value={formData.description} 
                onChange={handleChange} 
                rows={6} 
                required
                placeholder="Enter event description..."
                className="w-full px-2 py-1 focus:outline-none text-sm resize-none"
              />
            </div>
          </div>

          <div className="grid grid-cols-2 gap-6">
            <div>
              <label className="block text-sm font-medium text-blue-600 mb-2">Upload YT 1 Video Link</label>
              <input 
                type="url" 
                name="video1" 
                value={formData.video1} 
                onChange={handleChange} 
                placeholder="Paste Link : E.g https://www.youtube.com/watch?v=..." 
                className="border rounded-lg px-4 py-2 w-full text-sm focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-blue-600 mb-2">Upload YT 2 Video Link</label>
              <input 
                type="url" 
                name="video2" 
                value={formData.video2} 
                onChange={handleChange} 
                placeholder="Paste Link : E.g https://www.youtube.com/watch?v=..." 
                className="border rounded-lg px-4 py-2 w-full text-sm focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
              />
            </div>
          </div>

          <div className="grid grid-cols-2 gap-6">
            <div>
              <label className="block text-sm font-medium text-blue-600 mb-2">Upload image 1</label>
              <input 
                type="file" 
                accept="image/*"
                onChange={(e) => handleFileChange(e, 'image1')}
                className="border rounded-lg px-3 py-2 w-full text-sm"
              />
              {formData.image1 && (
                <p className="text-xs text-gray-500 mt-1">Selected: {formData.image1.name}</p>
              )}
            </div>
            <div>
              <label className="block text-sm font-medium text-blue-600 mb-2">Upload image 2</label>
              <input 
                type="file" 
                accept="image/*"
                onChange={(e) => handleFileChange(e, 'image2')}
                className="border rounded-lg px-3 py-2 w-full text-sm"
              />
              {formData.image2 && (
                <p className="text-xs text-gray-500 mt-1">Selected: {formData.image2.name}</p>
              )}
            </div>
          </div>

          <div className="flex items-center gap-2">
            <input 
              type="checkbox" 
              id="agree" 
              checked={agreeChecked}
              onChange={(e) => setAgreeChecked(e.target.checked)}
              required
              className="w-4 h-4"
            />
            <label htmlFor="agree" className="text-sm text-green-600">
              I agree I want To Upload The Above Data On website <span className="text-red-500">*</span>
            </label>
          </div>

          <button 
            type="submit" 
            disabled={isLoading}
            className={`px-6 py-2 text-sm font-medium rounded-lg transition-colors ${
              isLoading 
                ? 'bg-gray-400 cursor-not-allowed text-white' 
                : 'bg-blue-600 text-white hover:bg-blue-700'
            }`}
          >
            {isLoading ? (
              <span className="flex items-center gap-2">
                <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white"></div>
                Creating...
              </span>
            ) : (
              'Submit'
            )}
          </button>
        </form>
      </div>
    </>
  );
}
